body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

a {
  color: white;
}

a:visited{
  color: inherit;
}

.app {
  width: 100%;
  margin-top: 100px;
  margin-bottom: 60px;
  min-height: 300px;
  /*text-align: center;*/
  /*min-height: 500px*/
}

.title {
  font-size: 1.6em;
  min-height: 40px;
  position: absolute;
  left: 0;
  top: 0;
  border-radius: 0;
  width: 100%;
  text-align: left;
  background-color: #222530;
}

.flex-column {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.flex-row {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
}

.message-header {
  font-size: 1.2em;
  white-space: nowrap;
  text-overflow: ellipsis;
  max-width: 300px;
  overflow: hidden;
}

.message-to-user {
  transition: all 0.5s ease;
  font-weight: 500;
  position: fixed;
  width: 50%;
  left: 25%;
  height: 60px;
}

.translation-item {
  border: 2px solid rgb(210, 210, 210);
  border-bottom: none;
  border-radius: 0;
}

.translation-item-wrapper:first-child > .translation-item {
  border-top-left-radius: 6px;
  border-top-right-radius: 6px;
}

.translation-item-wrapper:last-child > .translation-item {
  border-bottom: 2px solid rgb(220, 220, 220);
  border-bottom-left-radius: 6px;
  border-bottom-right-radius: 6px;
}
